/* The actual Timeline (the vertical ruler) */
.Experience_Timeline_1CiZY {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

/* The actual Timeline (the vertical ruler) */
.Experience_Timeline_1CiZY::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

/* Container around content */
.Experience_Container_3WIJ0 {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

/* The circles on the Timeline */
.Experience_Container_3WIJ0::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background-color: white;
  border: 4px solid #FF9F55;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

/* Place the Container to the left */
.Experience_Left_1wBOG {
  left: 0;
}

/* Place the Container to the right */
.Experience_Right_wuQkS {
  left: 50%;
}

/* Add arrows to the left Container (pointing right) */
.Experience_Left_1wBOG::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid white;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent white;
}

/* Add arrows to the right Container (pointing left) */
.Experience_Right_wuQkS::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
}

/* Fix the circle for Containers on the right side */
.Experience_Right_wuQkS::after {
  left: -16px;
}

/* The actual content */
.Experience_Content_2dJ5O {
  padding: 20px 30px;
  background-color: white;
  position: relative;
  border-radius: 6px;
}

/* Media queries - Responsive Timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  .Experience_Experience_2x1Qx {
    margin-top: 50px;
    padding: 10px 10px 20px;
  }

  /* Place the timelime to the left */
  .Experience_Timeline_1CiZY::after {
    left: 31px;
  }
  
  /* Full-width Containers */
  .Experience_Container_3WIJ0 {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  
  /* Make sure that all arrows are pointing leftwards */
  .Experience_Container_3WIJ0::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .Experience_Left_1wBOG::after, .Experience_Right_wuQkS::after {
    left: 15px;
  }
  
  /* Make all right Containers behave like the left ones */
  .Experience_Right_wuQkS {
    left: 0%;
  }
}

